import { Component, OnInit } from '@angular/core';
import { PeliculaService } from "../../services/pelicula.service";
import { Pelicula } from "../../Pelicula";
import { DomSanitizer } from '@angular/platform-browser';
declare var $: any;
@Component({
  selector: 'app-peliculas',
  templateUrl: './peliculas.component.html',
  styleUrls: ['./peliculas.component.css']
})
export class PeliculasComponent implements OnInit {
  peliculas: Pelicula[];
  titulo: String;
  descripcion: String;
  portadatemp : String="imagenes/portadas/";
  trailer: string;
  estreno: String;
  generos: String;
  estado: number;
  peliAEditar: Pelicula = {
    titulo: "",
    descripcion: "",
    portada: "",
    trailer: "",
    estreno: "",
    generos: "",
  };
  urlp;

  constructor(private peliculaService: PeliculaService, private sanitizer: DomSanitizer) {
    this.peliculaService.getPeliculas().subscribe(pelis => {
      this.peliculas = pelis;
      this.estado = 0;
    })
  }


  //Transforma la url para que pueda ser agregada al iframe del trailer
  transform(url) {
    if (!url) { return ''; }
    if (url) {
      url = url.replace('watch?v=', 'embed/');
    }
    return this.sanitizer.bypassSecurityTrustResourceUrl(url);
  }



  //Agrega una pelicula 
  addPelicula(event) {
    event.preventDefault();
    console.log("addpeli",this.portadatemp)
    const newPelicula:Pelicula = {
      titulo: this.titulo,
      descripcion: this.descripcion,
      portada: this.portadatemp,
      trailer: this.trailer,
      estreno: this.estreno,
      generos: this.generos
    };


    this.peliculaService.addPeliculas(newPelicula).subscribe((peli: any) => {
      this.peliculas = peli;
    });
    this.estado = 0;
    $("#agregar").reset()
  }

  //Elimina una pelicula
  deletePelicula(id) {
    const pelicula = this.peliculas;
    this.peliculaService.deletePelicula(id).subscribe(data => {

      for (let i = 0; i < pelicula.length; i++) {
        if (pelicula[i]._id == id) {
          pelicula.splice(i, 1);
        }
      }

    })
  }




  //Habilita el modo edicion
  editarPelicula(pelicula) {
    this.estado = 2;

    this.peliAEditar = pelicula;
  }

  //Actualiza una pelicula
  updatePelicula(event) {
    $("#cancelarEdicion").click();
    event.preventDefault();
    const pelicula = {
      _id: this.peliAEditar._id,
      titulo: this.peliAEditar.titulo,
      descripcion: this.peliAEditar.descripcion,
      portada: this.peliAEditar.portada,
      trailer: this.peliAEditar.trailer,
      estreno: this.peliAEditar.estreno,
      generos: this.peliAEditar.generos
    };
    this.peliculaService.updatePelicula(pelicula).subscribe(res => {
    });


  }

  ngOnInit() {


  }


  selectedFile: File;

  onFileChanged(event) {
    this.selectedFile = event.target.files[0];
    const uploadData = new FormData();
    uploadData.append('portada', this.selectedFile, this.selectedFile.name);
    this.peliculaService.addPortada(uploadData).subscribe((peli: any) => {
      this.portadatemp=this.portadatemp+peli;
      console.log("onfilechange",this.portadatemp)
    });

  }


}
