import { Component, OnInit } from '@angular/core';
import { PeliculaService } from "../../services/pelicula.service";
import * as $ from 'jquery';

@Component({
    selector: 'app-navbar',
    templateUrl: './navbar.component.html',
    styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
    peliculas;
    constructor(private peliculaService: PeliculaService) { }

    ngOnInit() {

        $(document).ready(function() {

            $("#buscar").focusin(function() {
                if ($("#buscar").val() != "") {
                    $("#resultados").css("display", "");
                }
            });

            $(document).on("click", function(e) {
                var container = $("#resultados");
                if (!container.is(e.target) && !$("#buscar").is(e.target) && container.has(e.target).length === 0) {
                    $("#resultados").css("display", "none");
                }
            });
        });
    }

    vaciar() {
        $(document).ready(function() {



            $("#resultados").css("display", "none");
        });
    }
    buscar(event) {

        if (event.target.value == "") {
            this.peliculas = [];
            $(document).ready(function() {
                $("#resultados").css("display", "none");
            });
        } else {
            $(document).ready(function() {

                if ($("#buscar").val() != "") {
                    $("#resultados").css("display", "");
                }

            });

            this.peliculaService.getBuscarPelicula(event.target.value).subscribe(res => {
                this.peliculas = res;
                if (this.peliculas.length == 0) {
                    $(document).ready(function() {
                        $("#resultados").html("<li style='padding-bottom:5px' >No se encontraron resultados<li>")


                    });
                }
            });
        }
    }




}
