import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { Pelicula } from "../Pelicula";
import 'rxjs/add/operator/map';

@Injectable({
  providedIn: 'root'
})
export class PeliculaService {
  domain: String = "http://localhost:8080";
  //domain: String = "https://movierest.herokuapp.com";
  key: String = "67f4da3fd50b5f030400d2f3527b608e";
  constructor(private http: HttpClient) { }

  ngOnInit() {
  }

  //Obtiene las peliculas de la api
  getPeliculas() {
    return this.http.get<Pelicula[]>(`${this.domain}/api/peliculas/${this.key}`).map(res => res);
  }

  getUnaPelicula(id) {
    return this.http.get<Pelicula>(`${this.domain}/api/peliculas/detalles/${this.key}&${id}`).map(res => res);
  }


  getPeliculaSegunGenero(genero) {
    return this.http.get<Pelicula[]>(`${this.domain}/api/peliculas/genero/${this.key}&${genero}`).map(res => res);
  }

  getBuscarPelicula(titulo){
    return this.http.get<Pelicula[]>(`${this.domain}/api/peliculas/buscar/${this.key}&${titulo}`).map(res => res);
  }




  //Agrega una pelicula en la api
  addPeliculas(pelicula:Pelicula) {
    return this.http.post<Pelicula>(`${this.domain}/api/peliculas/${this.key}`,pelicula).map(res => res);
  }

  addPortada(portada) {
    return this.http.post<Pelicula>(`${this.domain}/api/subirportada/${this.key}`,portada).map(res => res);
  }

  //Actualiza una pelicula
  updatePelicula(nuevaPelicula) { 
    return this.http.put(`${this.domain}/api/peliculas/${this.key}&${nuevaPelicula._id}`, nuevaPelicula).map(res => res);
  }

  //Elimina una pelicula
  deletePelicula(id) {
    return this.http.delete(`${this.domain}/api/peliculas/${this.key}&${id}`).map(res => res);
  }
}
